import './style.css'
import './scripts/imageSetter.js'
import { getOS } from './scripts/checkOS.js'
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import * as dat from 'dat.gui'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader.js'
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import ASScroll from '@ashthornton/asscroll'

import Stats from 'stats.js'

import Plyr from 'plyr'

const player = new Plyr('#player')

/**
 * Stats
 */
const stats = new Stats()
stats.showPanel(0)
// document.body.appendChild(stats.dom)

let scrollingDown = false
let lastScroll = 0
let sceneReady = false
let renderTick = true

let isDesktop = window.innerWidth > 800


/**
 * Loaders
 */
const loaderElement = document.querySelector('.loader')
const loaderFillElement = document.querySelector('.loader-fill')
const loadingManager = new THREE.LoadingManager(
    // loaded
    () => {
        gsap.delayedCall(1, () => {
            console.log('loaded')
            loaderFillElement.style.transform = `translateY(-100%)`
            loaderElement.classList.add('hide')
        })
        gsap.delayedCall(3, () => {
            loaderElement.style.display = 'none'
        })
    },
    // progress
    (itemUrl, itemsLoaded, itemsTotal) => {
        const progressRatio = itemsLoaded / itemsTotal
        loaderFillElement.style.transform = `translateY(${100 - (progressRatio * 100)}%)`
    }
)
const gltfLoader = new GLTFLoader(loadingManager)

const dracoLoader = new DRACOLoader()
dracoLoader.setDecoderPath('draco/')

gltfLoader.setDRACOLoader(dracoLoader)

const textureLoader = new THREE.TextureLoader(loadingManager)
const colorTexture = textureLoader.load('textures/courtColor.png')
const heightTexture = textureLoader.load('textures/courtHeightMap.png')
const ballColorTexture = textureLoader.load('textures/sakanaball.jpg')

/**
 * Base
 */
// Debug
const gui = new dat.GUI()
gui.hide()
gui.width = 400
const debugObject = {}

// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()

scene.background = new THREE.Color('#f6b387')

const groundPhoneGroup = new THREE.Group()


/**
 * Models
 */
gltfLoader.load(
    'models/test-iphone-x.glb',
    (gltf) => {
        gltf.scene.scale.set(0.02, 0.02, 0.02)
        gltf.scene.position.set(-0.203, 0, -0.546)
        gltf.scene.rotation.set(Math.PI, Math.PI, Math.PI)
        gltf.scene.children[0].material.metalness = 0
        groundPhoneGroup.add(gltf.scene)
        sceneReady = true
    }
)

gltfLoader.load(
    'models/baskethoop.glb',
    (gltf) => {
        gltf.scene.scale.set(0.1, 0.1, 0.1)
        gltf.scene.position.set(-0.648, 0.036, -0.574)
        gltf.scene.rotation.set(Math.PI / 2, Math.PI / 2, 0)
        gltf.scene.children[0].material.metalness = 0
        groundPhoneGroup.add(gltf.scene)
        sceneReady = true
    }
)
const groundMaterial = new THREE.MeshStandardMaterial({
    transparent: true,
    side: THREE.DoubleSide
})

groundMaterial.map = colorTexture
groundMaterial.displacementMap = heightTexture
groundMaterial.displacementScale = 0.0047

gui.add(groundMaterial, 'displacementScale').min(0).max(0.1).step(0.0001).name('displacement scale')

const ground = new THREE.Mesh(
    new THREE.PlaneGeometry(2.2, 2.2, 500, 500),
    groundMaterial
)

ground.position.set(0, 0, -0.6)
groundPhoneGroup.position.set(0, 0.34, -1.09)
groundPhoneGroup.rotation.set(-1.17, 0, -0.81)
groundPhoneGroup.add(ground)

const ball = new THREE.Mesh(
    new THREE.SphereGeometry(0.06, 32, 32),
    new THREE.MeshStandardMaterial({
        color: 0xffffff,
        map: ballColorTexture
    })
)

ball.position.set(0.442, 0.306, -0.511)
groundPhoneGroup.add(ball)
scene.add(groundPhoneGroup)


/**
 * Lights
 */

// const light = new THREE.AmbientLight( 0xaaaaaa ) // soft white light
// scene.add(light)

var hemiLight = new THREE.HemisphereLight(0xffffff, 0xbbbbbb)
hemiLight.position.set(0, 1, 0)
scene.add(hemiLight)

gui.add(hemiLight.position, 'x').min(-5).max(5).step(0.001).name('H lightX')
gui.add(hemiLight.position, 'y').min(-5).max(5).step(0.001).name('H lightY')
gui.add(hemiLight.position, 'z').min(-5).max(5).step(0.001).name('H lightZ')

const directionalLight = new THREE.DirectionalLight('#ffffff', 3)
directionalLight.position.set(-0.511, -0.772, 0.75)
directionalLight.shadow.camera.far = 10
directionalLight.shadow.mapSize.set(256, 256)
directionalLight.intensity = 0.2
scene.add(directionalLight)

gui.add(directionalLight, 'intensity').min(0).max(10).step(0.001).name('lightIntensity')
gui.add(directionalLight.position, 'x').min(-5).max(5).step(0.001).name('lightX')
gui.add(directionalLight.position, 'y').min(-5).max(5).step(0.001).name('lightY')
gui.add(directionalLight.position, 'z').min(-5).max(5).step(0.001).name('lightZ')
/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

/**
 * Set DOM Position
 */

const screenTest = document.querySelector('.screen')
let point = new THREE.Vector3(-0.53, 0, -5)


window.addEventListener('resize', () => {
    isDesktop = window.innerWidth > 800

    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.left = - 1 * sizes.width / sizes.height
    camera.right = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

    displayCorrectMobileCTA()

})

/**
 * Camera
 */

const aspectRatio = sizes.width / sizes.height
const camera = new THREE.OrthographicCamera(- 1 * aspectRatio, 1 * aspectRatio, 1, - 1, 0.01, 1000)
scene.add(camera)

// Controls
const controls = new OrbitControls(camera, canvas)
controls.enableDamping = true

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    antialias: true
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

/**
 * Animate
 */
const tick = () => {
    stats.begin()
    // Update controls
    controls.update()

    // Update DOM
    if (sceneReady) {
        camera.updateMatrixWorld(true)
        const screenPosition = point.clone()
        screenPosition.project(camera)
        const translateX = screenPosition.x * sizes.width * 0.5
        const translateY = - screenPosition.y * sizes.width * 0.5
        if (isDesktop) screenTest.style.transform = `translate(${translateX - screenTest.offsetWidth / 2 - 1}px, ${translateY - screenTest.offsetHeight / 2}px)`
    }

    // Render
    renderer.render(scene, camera)

    stats.end()
}


gsap.registerPlugin(ScrollTrigger)

const asscroll = new ASScroll(
    {
        customScrollbar: true
    }
)

ScrollTrigger.defaults(
    {
        scroller: asscroll.Scroll.scrollContainer
    }
)

ScrollTrigger.scrollerProxy(asscroll.Scroll.scrollContainer,
    {
        scrollTop(value) {
            return arguments.length ? asscroll.scrollTo(value) : -asscroll.smoothScrollPos;
        },
        getBoundingClientRect() {
            return { top: 0, left: 0, width: window.innerWidth, height: window.innerHeight }
        }
    })

const onRequestAnimationFrame = () => {
    if (renderTick) tick()
    ScrollTrigger.update()
}

const scroll = { value: 0 }
const anchors = document.getElementsByClassName('anchor')

asscroll.on('raf', onRequestAnimationFrame)
ScrollTrigger.addEventListener('refresh', () => { asscroll.onResize() })

const checkScrollDirection = (e) => {
    scrollingDown = e < lastScroll
    lastScroll = e
}
asscroll.on('scroll', checkScrollDirection)

const options =
{
    rootMargin: '0px',
    threshold: 0.9
}

const observeFeature = (animatedElementSelector, targetSelector) => {
    const animatedElement = document.querySelector(animatedElementSelector)
    const observer = new IntersectionObserver(entries => {
        if (isDesktop) {
            entries.forEach((entry) => {
                if (entry.isIntersecting) {
                    animatedElement.style.opacity = 1

                } else {
                    animatedElement.style.opacity = 0
                    animatedElement.style.zIndex = -1
                }
            }
            )
        }
    }, options)
    observer.observe(document.querySelector(targetSelector))
}

const visionAppObserver = (animatedElement, color) => {
    return new IntersectionObserver(entries => {
        if (isDesktop) {
            entries.forEach((entry) => {
                if (entry.isIntersecting) {
                    animatedElement.style.backgroundColor = color
                    renderTick = color === 'transparent'
                }
            }
            )
        }
    },
        options
    )
}

const observeForNavBar = () => {
    const animatedElement = document.querySelector('nav.nav')
    visionAppObserver(animatedElement, '#f6b387').observe(document.querySelector('.vision'))
    visionAppObserver(animatedElement, 'transparent').observe(document.querySelector('.app-game'))
}

const displayCorrectMobileCTA = () => {
    document.querySelector('.android-cta').style.display = getOS() === 'iOS' && !isDesktop ? 'none' : ''
    document.querySelector('.ios-cta').style.display = !isDesktop && getOS() !== 'iOS' ? 'none' : ''
}

window.addEventListener('load', () => {
    displayCorrectMobileCTA()
    gsap.to('.scroll',
        {
            scrollTrigger:
            {
                pin: true,
                scrub: true,
                trigger: '.text'
            },
            y: 0,
            ease: 'none'
        })

    gsap.to('.scroll',
        {
            scrollTrigger:
            {
                pin: true,
                scrub: true,
                trigger: '.ballons'
            },
            duration: 1,
            opacity: 0
        }
    )
    gsap.to(groundPhoneGroup.position,
        {
            scrollTrigger:
            {
                pin: true,
                scrub: true,
                trigger: '.ballons'
            },
            duration: 3,
            x: 1.5,
            y: 0,
            z: -5
        }
    )

    gsap.to(groundPhoneGroup.rotation,
        {
            scrollTrigger: {
                pin: true,
                scrub: true,
                trigger: '.ballons'
            },
            duration: 3,
            x: 0,
            z: 0
        }
    )

    gsap.to(groundPhoneGroup.scale,
        {
            scrollTrigger:
            {
                pin: true,
                scrub: 1.5,
                trigger: '.ballons'
            },
            duration: 3,
            x: 10,
            y: 10,
            z: 10
        }
    )


    asscroll.enable()
    /**
     * Intersection Observer
     */

    observeFeature('.location-feature', '.app-location .app-content')
    observeFeature('.invitation-feature', '.app-invitation .app-content')
    observeFeature('.game-feature', '.app-game .app-content')
    observeForNavBar()

})


const scrollToSection = (target) => {
    scroll.value = -asscroll.smoothScrollPos + target.getBoundingClientRect().top - 120
    asscroll.scrollTo(scroll.value)
}


const handleAnchors = () => {
    for (const anchor of anchors) {
        const target = document.querySelector(`.${anchor.id.split('-')[1]}`)
        anchor.addEventListener('click', () => { scrollToSection(target) })
    }
}

handleAnchors()