/**
 * set dom image
 */

const addImage = (root, parentSelector, imageClass) => {
    const image = new Image()
    image.src = root
    image.classList.add(imageClass)
    document.querySelector(parentSelector).append(image)
}

addImage('images/location-feature.png', '.screen-wrap', 'location-feature')
addImage('images/invitation-feature.png', '.screen-wrap', 'invitation-feature')
addImage('images/game-feature.png', '.screen-wrap', 'game-feature')
